@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");

.pekush-calendar {
  font-family: "Inter", sans-serif;
}

.pekush-calendar .wrap {
  width: 250px;
  background: #ffffff;
  box-shadow: 0px 3.735px 19.92px rgba(0, 0, 0, 0.2);
  border-radius: 12.45px;
  padding: 15px;
}

.pekush-calendar .header {
  display: flex;
  justify-content: space-between;
}

.pekush-calendar .header h3 {
  font-weight: 700;
  font-size: 15px;
  margin: 0px 10px 0px 0px;
  display: inline-flex;
  text-transform: capitalize;
}

.pekush-calendar .header p {
  display: inline-flex;
  margin: 0px;
  font-size: 15px;
  font-weight: 100;
}

.pekush-calendar .arrow {
  border: none;
  background: none;
  display: inline-flex;
  cursor: pointer;
}

.pekush-calendar .arrow.right {
  transform: rotate(180deg);
}

.pekush-calendar .content {
  margin-top: 30px;
}

.pekush-calendar .content .weeks {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  text-transform: capitalize;
  font-size: 10px;
  color: #aaaaaa;
}

.pekush-calendar .content .weeks .item:nth-child(6n),
.pekush-calendar .content .weeks .item:nth-child(7n) {
  color: #e03e0a;
}

.pekush-calendar .content .dates {
  margin-top: 10px;
}
.pekush-calendar .content .dates .row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 10px;
  font-weight: bold;
  line-height: 20px;
}

.pekush-calendar .content .dates .row .date {
  cursor: pointer;
}

.pekush-calendar .content .dates .row .date:nth-child(6n),
.pekush-calendar .content .dates .row .date:nth-child(7n) {
  color: #e03e0a;
}

.pekush-calendar .content .dates .row .date.disable {
  color: #aaaaaa;
  cursor: auto;
}
